import './Cards.css'

function Free_Card(props) {
    return (
        <div className="col-11 col-md-4">
            <div className="card mb-5 mb-lg-0">
                <div className="card-body">
                    <h5 className="card-title text-muted text-uppercase text-center fs-6">Free</h5>
                    <h6 className="text-center fs-1">$0<span className="fs-6 fw-normal">/month</span></h6>
                    <hr />
                    <ul className="px-3">
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Single User</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>5GB Storage</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Unlimited Public Projects</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Community Access</li>
                        <li className="text-muted mb-2"><span><i className="mx-2 fas fa-times"></i></span>Unlimited Private Projects</li>
                        <li className="text-muted mb-2"><span><i className="mx-2 fas fa-times"></i></span>Dedicated Phone Support</li>
                        <li className="text-muted mb-2"><span><i className="mx-2 fas fa-times"></i></span>Free Subdomain</li>
                        <li className="text-muted mb-2"><span><i className="mx-2 fas fa-times"></i></span>Monthly Status Reports</li>
                    </ul>
                    <div className="d-grid">
                        <button className="btn btn-primary text-uppercase rounded-pill py-3">Button</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Free_Card;