import './Cards.css'

function Pro_Card(props) {
    return (
        <div className="col-11 col-md-4">
            <div className="card mb-5 mb-lg-0">
                <div className="card-body">
                    <h5 className="card-title text-muted text-uppercase text-center fs-6">Pro</h5>
                    <h6 className="text-center fs-1">$49<span className="fs-6 fw-normal">/month</span></h6>
                    <hr />
                    <ul className="px-3">
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Unlimited Users</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>150GB Storage</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Unlimited Public Projects</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Community Access</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Unlimited Private Projects</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Dedicated Phone Support</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span><span className="fw-bold">Unlimited </span>Free Subdomain</li>
                        <li className="mb-2"><span><i className="mx-2 fas fa-check"></i></span>Monthly Status Reports</li>
                    </ul>
                    <div className="d-grid">
                        <button className="btn btn-primary text-uppercase rounded-pill py-3">Button</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Pro_Card;